<template>
<div class="Postpost">
  <span v-if="PostpostTitle"></span>
  <span v-else></span>
  <input type="text" placeholder="" @click="PostpostTitleF(1)" v-model="postTitle" />
  <span v-if="PostpostThings"></span>
  <span v-else></span>
  <textarea placeholder="" @click="PostpostTitleF(2)" v-model="postContent"></textarea>
  <i @click="addImgs" class="iconfont icon-picture"></i>
</div>
</template>

<script>
import { createPost } from '../fetch/posts'
import { UploaderBuilder, Uploader } from 'qiniu4js'
import store from '../vuex/store'
import { getQiniuToken } from '../fetch/qiniu'

export default {
  name: 'postpost',
  data () {
    return {
      PostpostTitle: false,
      PostpostThings: false,
      postTitle: '',
      postContent: '',
      imgUrls: 'http://7xlx4u.com1.z0.glb.clouddn.com/',
      imgArr: []
    }
  },
  methods: {
    PostpostTitleF (n) {
      let self = this;
      self.PostpostTitle = false;
      self.PostpostThings = false;
      switch (n){
        case 1:
          self.PostpostTitle = true;
          break;
        case 2:
          self.PostpostThings = true;
          break;
      }
    },
    pushPost () {
      createPost(this.$router.params.postId, {
        title: this.postTitle,
        content: this.postContent
      }).then(response => {
        console.log("ID: "+response.id)
        this.$Message.success('')
      }).catch(error => {
        console.log(": "+error)
        this.$Message.error(''+error)
      })
    },
    addImgs (event) {
      var self = this;
      // qiniu4js uploader object
  var urlkey='';
  var uploader = new UploaderBuilder()
    .debug(false)//debugfalse
    .domain({http: "http://upload.qiniu.com", https: "https://up.qbox.me"})
    .retry(2)//0
    .compress(0.5)//1,0-1
    .scale([200,0])  //,[200,0],.[0,100],.[200,100]
    .size(1024*1024)
    .chunk(true)
    .auto(true)
    .multiple(false)
    .accept(['.gif','.png','.jpg'])//http://www.w3schools.com/tags/
    .tokenShare(true)
    .tokenFunc(function (setToken,task) {
      getQiniuToken().then(res => {
        setToken(res.uploadToken);
      })
    })
    //
    .interceptor({
        //,true
      onIntercept: function (task) {
        return task.file.size > 4 * 1024 * 1024;
      },
      //true
      onInterrupt: function (task) {
        if (this.onIntercept(task)) {
          self.$Message.warning('4m')
          return true;
        }
        else {
          return false;
        }
      }
    }) 
    .listener({
      onReady(tasks) {
          //,task.file
        //,
        
      },onStart(tasks){
          //
        //
        console.log('upload start')
        
      },onTaskGetKey(task){
        
      },onTaskProgress: function (task) {
          //,`task.progress`
        console.log(task.progress);
        
      },onTaskSuccess(task){
        //
        console.log(task.result.key);//key
        urlkey=task.result.key;
        self.imgsAdd(urlkey)
      },onTaskFail(task) {
        //
        
      },onTaskRetry(task) {
        //
        
      },onFinish(tasks){
          //HTTP()
              
      }
    }).build();
      uploader.chooseFile();
      event.stopPropagation();
    },
    imgsAdd (urlkey) {
      var self = this
      if (urlkey !== '') {
          urlkey = "http://7xlx4u.com1.z0.glb.clouddn.com/" + urlkey;
          if (urlkey !== 'http://7xlx4u.com1.z0.glb.clouddn.com/') {
            if (urlkey !== this.imgUrls) {
              self.postContent = self.postContent + "[]"
              console.log(self.postContent)
              self.imgArr.push(urlkey)
              self.imgUrls = urlkey
            }
          }
          urlkey = ""
        }
    }
  },
  watch: {
    postContent: function () {
      var contents = this.postContent
      var ind = contents.indexOf('[]')
      var i = 0
      while (ind > -1){
      	contents = contents.replace(/\[\]/, "<img src='" + this.imgArr[i] + "' />")
      	i++
      	ind = contents.indexOf('[]')
      }
      store.dispatch('setPostContent', {
        title: this.postTitle,
        content: contents
      })
    }
  },
  mounted () {
    if (this.$route.query.edit == true) {
      this.postTitle = store.state.postEditContent.title
      this.postContent = store.state.postEditContent.content
    }
  }
}
</script>

<style lang="scss" scoped>
@import '~simplemde/dist/simplemde.min.css';
.Postpost{
  padding: 15px 35px 0 35px;
  > span{
    display: block;
    width: 100%;
    height: 16.5px;
    font-size: 12px;
    line-height: 16.5px;
    text-align: left;
    padding-left: 8px;
    color: rgba(86,119,252,0.87);
  }
  input{
    font-size: 16px;
    line-height: 22.5px;
    color: rgba(0,0,0,0.87);
    width: 100%;
    padding: 8px 8px;
    margin-bottom: 17px;
    box-sizing: border-box;
    border: none;
    outline: none;
    border-bottom: 0.5px solid rgba(86,119,252,0.54);
    &::-webkit-input-placeholder{
      color: rgba(0,0,0,0.38);
    }
    &:focus{
      border-bottom: 2px solid rgba(86,119,252,0.87);
    }
  }
  textarea{
    box-sizing: border-box;
    background: #FAFAFA;
    box-shadow: 0 0 1px 0 rgba(0,0,0,0.12), 0 1px 1px 0 rgba(0,0,0,0.24);
    border-radius: 2px;
    width: 100%;
    height: 60vh;
    resize: none;
    margin-top: 9px;
    margin-bottom: 12px;
    padding: 14px 8px 0 8px;
    font-size: 14px;
    line-height: 20px;
    color: rgba(0,0,0,0.87);
    letter-spacing: 0.5px;
    outline: none;
    &::-webkit-input-placeholder{
      color: rgba(0,0,0,0.38);
    }
  }
  > div{
    width: 100%;
    text-align: left;
    box-sizing: border-box;
    font-size: 14px;
  }
  i{
    color: #5677FC;
    display: block;
    margin-left: 8px;
    text-align: left;
  }
}
</style>


